import { createSelector } from 'reselect';

export const getLicensesData = createSelector(
  [(state) => state.licenseAllocation.licenses],
  (licenses) => licenses
);

export const licensesLoading = createSelector(
  [(state) => state.licenseAllocation.flags.licenses.loading],
  (isLoading) => isLoading
);

export const getDbLicensesData = createSelector(
  [(state) => state.licenseAllocation.dbLicenses],
  (licenses) => licenses
);

export const dbLicensesLoading = createSelector(
  [(state) => state.licenseAllocation.flags.dbLicenses.loading],
  (isLoading) => isLoading
);

export const getLicenseTargetsData = createSelector(
  [(state) => state.licenseAllocation.licenseTargets],
  (licenses) => licenses
);

export const licenseTargetsLoading = createSelector(
  [(state) => state.licenseAllocation.flags.licenseTargets.loading],
  (isLoading) => isLoading
);

export const getAvailableServersData = createSelector(
  [(state) => state.licenseAllocation.availableServers],
  (licenses) => licenses
);

export const availableServersLoading = createSelector(
  [(state) => state.licenseAllocation.flags.availableServers.loading],
  (isLoading) => isLoading
);
