import { createReducer, types } from 'shared/lib/redux-utils';
import { LOAD_AVAILABLE_SERVERS, LOAD_DB_LICENSES, LOAD_LICENSE_TARGETS, LOAD_LICENSES } from '../actions';

export const initialState = {
  licenses: [],
  dbLicenses: [],
  licenseTargets: [],
  availableServers: [],
  flags: {
    licenses: { loading: false },
    dbLicenses: { loading: false },
    licenseTargets: { loading: false },
    availableServers: { loading: false },
  }
};

export const producers = {
  [types.loading(LOAD_LICENSES)]: draft => {
    draft.flags.licenses.loading = true;
  },
  [types.success(LOAD_LICENSES)]: (draft, { payload }) => {
    draft.licenses = payload;
    draft.flags.licenses.loading = false;
  },
  [types.fail(LOAD_LICENSES)]: draft => {
    draft.flags.licenses.loading = false;
  },

  [types.loading(LOAD_DB_LICENSES)]: draft => {
    draft.flags.dbLicenses.loading = true;
  },
  [types.success(LOAD_DB_LICENSES)]: (draft, { payload }) => {
    draft.dbLicenses = payload;
    draft.flags.dbLicenses.loading = false;
  },
  [types.fail(LOAD_DB_LICENSES)]: draft => {
    draft.flags.dbLicenses.loading = false;
  },

  [types.loading(LOAD_LICENSE_TARGETS)]: draft => {
    draft.flags.licenseTargets.loading = true;
  },
  [types.success(LOAD_LICENSE_TARGETS)]: (draft, { payload }) => {
    draft.licenseTargets = payload;
    draft.flags.licenseTargets.loading = false;
  },
  [types.fail(LOAD_LICENSE_TARGETS)]: draft => {
    draft.flags.licenseTargets.loading = false;
  },

  [types.loading(LOAD_AVAILABLE_SERVERS)]: draft => {
    draft.flags.availableServers.loading = true;
  },
  [types.success(LOAD_AVAILABLE_SERVERS)]: (draft, { payload }) => {
    draft.availableServers = payload;
    draft.flags.availableServers.loading = false;
  },
  [types.fail(LOAD_AVAILABLE_SERVERS)]: draft => {
    draft.flags.availableServers.loading = false;
  },
};

export default createReducer(producers, initialState);
