import React from 'react';
import { connect } from 'react-redux';
import { getFullPath, getMssqlStorageDetails } from '../../../store/selectors';
import { loadMssqlStorageDataThunk } from '../../../store/thunks';
import PropTypes from 'prop-types';
import { LOAD_MSSQL_DISK_IO, LOAD_MSSQL_FILE_STORAGE, LOAD_MSSQL_STORAGE_USED } from '../../../store/actions';
import { Center, Table, LightTooltip } from 'shared/components';
import { Loader } from 'shared/components/loaders';
import { formatTimePeriod } from '../../../../license-collection/utils/formatTimePeriod';
import { Cell, Pie, PieChart, ResponsiveContainer, Tooltip } from 'recharts';

import './MssqlStorage.local.scss';
import { formatMemorySizeGb } from '../../../../license-collection/utils/formatMemoryZiseGb';

const CellWithTooltip = ({ name }) => {
  if (name && name.length > 16) {
    return (
      <LightTooltip placement="bottom" title={<span>{name}</span>}>
        <span className="storage-cell ellipsis">{name}</span>
      </LightTooltip>
    );
  }
  return <span className="storage-cell">{name}</span>;
};

const diskIOColumns = [
  { label: 'Database Name' },
  { label: 'Physical Name' },
  { label: 'ID' },
  { label: 'IO Stall' },
  { label: 'IO Stall Queued Read' },
  { label: 'IO Stall Queued Write' },
  { label: 'IO Stall Read' },
  { label: 'IO Stall Write' },
  { label: 'Number of Bytes Read' },
  { label: 'Number of Bytes Written' },
  { label: 'Number of Reads' },
  { label: 'Number of Writes' },
  { label: 'Sample' },
  { label: 'Size On Disk (GB)' },
  { label: 'Type Desc' },
];

const diskIOTableProps = { columns: diskIOColumns, sortBehavior: { sortKey: 'mockedColumn', order: 'desc' }, changeSort: () => {} };

const diskIORowRenderer = (row, index) => (
  <Table.Row hover key={`${row.Database_name}_${index}`} className="storage-row">
    <Table.Cell>
      <CellWithTooltip name={row.Database_name} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.physical_name} />
    </Table.Cell>
    <Table.Cell>{row.dbid}</Table.Cell>
    <Table.Cell>{formatTimePeriod(row.io_stall)}</Table.Cell>
    <Table.Cell>{formatTimePeriod(row.io_stall_queued_read_ms)}</Table.Cell>
    <Table.Cell>{formatTimePeriod(row.io_stall_queued_write_ms)}</Table.Cell>
    <Table.Cell>{formatTimePeriod(row.io_stall_read_ms)}</Table.Cell>
    <Table.Cell>{formatTimePeriod(row.io_stall_write_ms)}</Table.Cell>
    <Table.Cell>{row.num_of_bytes_read}</Table.Cell>
    <Table.Cell>{row.num_of_bytes_written}</Table.Cell>
    <Table.Cell>{row.num_of_reads}</Table.Cell>
    <Table.Cell>{row.num_of_writes}</Table.Cell>
    <Table.Cell>{formatTimePeriod(row.sample_ms)}</Table.Cell>
    <Table.Cell>{formatMemorySizeGb(row.size_on_disk_bytes)}</Table.Cell>
    <Table.Cell>{row.type_desc}</Table.Cell>
  </Table.Row>
);

const fileStorageColumns = [
  { label: 'Name' },
  { label: 'Physical Name' },
  { label: 'Data Space ID' },
  { label: 'File ID' },
  { label: 'Growth' },
  { label: 'Size' },
  { label: 'Max Size' },
  { label: 'State' },
  { label: 'State Desc' },
  { label: 'Type' },
  { label: 'Type Desc' },
];

const fileStorageTableProps = { columns: fileStorageColumns, sortBehavior: { sortKey: 'mockedColumn', order: 'desc' }, changeSort: () => {} };

const fileStorageRowRenderer = (row, index) => (
  <Table.Row hover key={`${row.name}_${index}`} className="storage-row">
    <Table.Cell>
      <CellWithTooltip name={row.name} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.physical_name} />
    </Table.Cell>
    <Table.Cell>{row.data_space_id}</Table.Cell>
    <Table.Cell>{row.file_id}</Table.Cell>
    <Table.Cell>{row.growth}</Table.Cell>
    <Table.Cell>{row.size}</Table.Cell>
    <Table.Cell>{row.max_size}</Table.Cell>
    <Table.Cell>{row.state}</Table.Cell>
    <Table.Cell>{row.state_desc}</Table.Cell>
    <Table.Cell>{row.type}</Table.Cell>
    <Table.Cell>{row.type_desc}</Table.Cell>
  </Table.Row>
);

const PieChartColors = [
  '#176BA0', '#A6EAA5', '#E8F9F5', '#1AC9E6', '#6DF0D2',
  '#7D3AC1', '#EA7369', '#1DE4BD', '#EF7E32', '#F0A58F',
  '#C02323', '#DB4CB2', '#4C6BDB', '#F1DE2C', '#399137'
];

const PieChartTooltip = (entry) => {
  const { payload } = entry;
  if (!payload.length) {
    return '';
  }
  const { label, value } = payload[0].payload;
  return (
    <span className="pie-chart-tooltip">{label}: {formatMemorySizeGb(value)} GB</span>
  );
};

class MssqlStorage extends React.PureComponent {
  componentDidMount() {
    const { fullPath, getMssqlStorageData } = this.props;
    // timeouts needed for BE to be ready to process next request
    getMssqlStorageData({ fullPath, process: 'vendita.microsoft.sqlserver.monitor.storage.DiskFileIO', storageKey: LOAD_MSSQL_DISK_IO });
    setTimeout(() => {
      getMssqlStorageData({ fullPath, process: 'vendita.microsoft.sqlserver.monitor.storage.StorageUsed', storageKey: LOAD_MSSQL_STORAGE_USED });
    }, 1000);
    setTimeout(() => {
      getMssqlStorageData({ fullPath, process: 'vendita.microsoft.sqlserver.monitor.storage.file_storage', storageKey: LOAD_MSSQL_FILE_STORAGE });
    }, 2000);
  }

  render() {
    const { storageData, loadingFlags } = this.props;
    const dataLoading = loadingFlags.loadingDiskIO || loadingFlags.loadingStorageUsed || loadingFlags.loadingFileStorage;
    const usedStorageInfo = storageData.storageUsed && storageData.storageUsed.length ? storageData.storageUsed[0] : null;
    const hasFileStorageInfo = storageData.fileStorage && storageData.fileStorage.length;
    const hasDiskIOInfo = storageData.diskIO && storageData.diskIO.length;
    const hasAnyData = !!usedStorageInfo || hasDiskIOInfo || hasFileStorageInfo;
    let storagePieData = [];

    if (dataLoading) {
      return (
        <Center>
          <Loader>Loading data...</Loader>
        </Center>
      );
    }

    if (!hasAnyData) {
      return (
        <Center>
          No Storage Data Available.
        </Center>
      );
    }

    if (hasDiskIOInfo) {
      storagePieData = this.prepareStorageGraphData(storageData);
    }

    return (
      <div className="mssql-storage-container">
        {usedStorageInfo ? (
          <div className="mssql-storage-used">
            <div className="mssql-storage-used-label">Database Size:</div>
            <div className="mssql-storage-used-value">{usedStorageInfo.database_size}</div>
            <div className="mssql-storage-used-label">Unallocated Space:</div>
            <div className="mssql-storage-used-value">{usedStorageInfo.unallocated_space}</div>
          </div>
        ) : ''}
        {hasFileStorageInfo ? (
          <div>
            <h3 className="mssql-table-title">File Storage</h3>
            <Table {...fileStorageTableProps} className="table">
              {storageData.fileStorage.map((db, index) => fileStorageRowRenderer(db, index))}
            </Table>
          </div>
        ) : '' }
        {hasDiskIOInfo ? (
          <>
            <h3 className="mssql-table-title">Disk IO</h3>
            <Table {...diskIOTableProps} className="table">
              {storageData.diskIO.map((db, index) => diskIORowRenderer(db, index))}
            </Table>
            {storagePieData.length && (
              <ResponsiveContainer height={400}>
                <PieChart>
                  <Pie data={storagePieData} dataKey="value" cx="50%" cy="50%" innerRadius={0} outerRadius={150}>
                    {storagePieData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={PieChartColors[index]} />
                    ))}
                  </Pie>
                  <Tooltip content={PieChartTooltip} />
                </PieChart>
              </ResponsiveContainer>
            )}
          </>
        ) : ''}
      </div>
    );
  }

  prepareStorageGraphData = (storageData) => {
    const logsStorageSize = storageData.diskIO
      .filter((item) => item.type_desc === 'LOG')
      .map((item) => item.size_on_disk_bytes)
      .reduce((sum, item) => {
        sum += +item;
        return sum;
      }, 0);

    return storageData.diskIO
      .filter((item) => item.type_desc === 'ROWS')
      .map((item) => {
        return {
          label: item.Database_name,
          value: item.size_on_disk_bytes
        };
      }).concat([{
        label: 'All logs',
        value: logsStorageSize
      }]);
  }
}

MssqlStorage.propTypes = {
  loadingFlags: PropTypes.object,
  fullPath: PropTypes.string,
  getMssqlStorageData: PropTypes.func
};

export const ConnectedMssqlStorage = connect(
  state => ({
    loadingFlags: state.explorer.flags.storage,
    fullPath: getFullPath(state),
    storageData: getMssqlStorageDetails(state)
  }),
  dispatch => ({
    getMssqlStorageData: filters => dispatch(loadMssqlStorageDataThunk(filters))
  })
)(MssqlStorage);
