import React, { useEffect, useRef } from 'react';
import { Modal, ModalHeader } from 'shared/components/overlays';
import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiLicense } from '@mdi/js';
import { connect } from 'react-redux';
import { getAvailableServersData, getLicensesData } from '../../store/selectors';
import { Text } from 'shared/components/form/editors/Generic';
import { Column, Paper, Row } from 'shared/components/layout';
import { Form, Formik } from 'formik';
import {
  loadAvailableServersThunk,
  loadLicenseAllocationThunk,
  updateLicenseAssignmentThunk
} from '../../store/thunks';
import { Integer } from '../../../shared/components/form/editors/Generic';
import { SubmitButtonNoDirty } from '../../../shared/components/form/SubmitButtonNoDirty';

import './AssignLicenseModal.local.scss';

export const AssignLicenseModal = ({ modalActions, open, selectedAssignment }) => {
  return (
    <>
      <Modal open={open} actions={modalActions}>
        <ConnectedAssignLicense closeModal={modalActions.close} selectedAssignment={selectedAssignment} />
      </Modal>
    </>
  );
};

const AssignLicense = (
  {
    licensesData,
    availableServers,
    getAvailableServers,
    updateLicenseAssignment,
    selectedAssignment,
    closeModal,
    refreshLicenses
  }) => {
  const formRef = useRef();
  const initialValues = {
    server_id: selectedAssignment.server_id || '',
    license_id: selectedAssignment.license_id || '',
    metric: selectedAssignment.license_metric || '',
    term: selectedAssignment.license_term || '',
    type: selectedAssignment.license_type || '',
    quantity: selectedAssignment.licenses_available || '',
  };

  const extraValidations = (values) => {
    const errors = {};
    if (values.license_id) {
      const selectedLicense = licensesData.find((license) => license.license_id === values.license_id);
      if (selectedLicense) {
        const available = selectedLicense.licenses_available || 0;
        const assigned = selectedLicense.licenses_assigned || 0;

        if (!values.quantity) {
          errors.quantity = 'Please specify positive or negative quantity';
        } else if (values.quantity < 0 && Math.abs(values.quantity) > assigned) {
          errors.quantity = `You have only ${assigned} assigned licenses. You can't decrease more.`;
        } else if (values.quantity > 0 && values.quantity > available) {
          errors.quantity = `You have only ${available} available licenses`;
        }
      }
    }
    return errors;
  };

  const licenseOptions = (licensesData || []).map((license) => {
    return {
      label: license.product_name,
      value: license.license_id,
      disabled: license.licenses_available <= 0
    };
  });

  const availableServersOptions = (availableServers || []).map((serverInfo) => {
    return {
      label: `${serverInfo.host_name} (${serverInfo.ip_address})`,
      value: serverInfo.server_id
    };
  });

  useEffect(() => {
    getAvailableServers();
  }, []);

  return (
    <Paper
      style={{
        width: '700px',
        minHeight: '565px',
        maxHeight: '800px'
      }}
    >
      <ModalHeader
        icon={
          <SvgIcon>
            <path d={mdiLicense} />
          </SvgIcon>
        }
        title="Assign Licenses"
        desc="Select target server and assign licenses to it"
        formikRef={formRef}
      />
      <Formik
        initialValues={initialValues}
        validate={extraValidations}
        onSubmit={(values) => {
          const isInitialAssignment = !selectedAssignment.server_id;
          updateLicenseAssignment(isInitialAssignment, values).then(() => {
            refreshLicenses();
            closeModal();
          });
        }}
        innerRef={formRef}
      >
        {() => {
          return (
            <>
              <Paper.Content>
                <Form className="assign-license-form">
                  <Row>
                    <Column xs={12}>
                      <Text
                        type="text"
                        name="server_id"
                        label="Server"
                        options={availableServersOptions}
                        value={initialValues.server_id}
                        required
                      />
                    </Column>
                  </Row>
                  <Row>
                    <Column xs={12}>
                      <Text
                        type="text"
                        name="license_id"
                        label="License"
                        options={licenseOptions}
                        value={initialValues.license_id}
                        disabled
                        required
                      />
                    </Column>
                  </Row>
                  <Row>
                    <Column xs="6">
                      <Text
                        type="text"
                        name="term"
                        label="Term"
                        value={initialValues.term}
                        disabled
                      />
                    </Column>
                    <Column xs="6">
                      <Text
                        type="text"
                        name="type"
                        label="Type"
                        value={initialValues.type}
                        disabled
                        required
                      />
                    </Column>
                  </Row>
                  <Row>
                    <Column xs="8">
                      <Text
                        type="text"
                        name="metric"
                        label="Metric"
                        value={initialValues.metric}
                        disabled
                      />
                    </Column>
                    <Column xs="4">
                      <Integer
                        type="text"
                        name="quantity"
                        label="Quantity"
                        value={initialValues.quantity}
                        required
                      />
                    </Column>
                  </Row>
                </Form>
              </Paper.Content>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <SubmitButtonNoDirty variant="contained" confirmation>
                    Submit
                  </SubmitButtonNoDirty>
                </Paper.Footer.Right>
              </Paper.Footer>
            </>
          );
        }}
      </Formik>
    </Paper>
  );
};

export const ConnectedAssignLicense = connect(
  state => ({
    licensesData: getLicensesData(state),
    availableServers: getAvailableServersData(state),
  }),
  dispatch => ({
    getAvailableServers: () => dispatch(loadAvailableServersThunk()),
    updateLicenseAssignment: (isInitialAssignment, payload) => dispatch(updateLicenseAssignmentThunk(isInitialAssignment, payload)),
    refreshLicenses: () => dispatch(loadLicenseAllocationThunk())
  })
)(AssignLicense);
