import { creators } from 'shared/lib/redux-utils';
import { LOAD_AVAILABLE_SERVERS, LOAD_DB_LICENSES, LOAD_LICENSE_TARGETS, LOAD_LICENSES } from '../actions';
import { RestClient } from 'shared/lib/rest';
import { CONSTANTS } from 'config';

const licensesInfoMock = {
  license_id: 1,
  product_name: 'Oracle ADV',
  product_id: 1,
  metric: 1,
  lic_total: 5,
  lic_assigned: 3,
  lic_avail: 2,
};

const licenseTargetsMock = {
  server_id: 1,
  server: 'Oracle Base',
  database: 'ORA112SE',
  quantity: 5,
};

export function loadLicenseAllocationThunk() {
  return async dispatch => {
    dispatch(creators.loading(LOAD_LICENSES));
    try {
      const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.LICENSES_INFO);
      dispatch(creators.success(LOAD_LICENSES, response.data.data || []));
    } catch (error) {
      dispatch(creators.fail(LOAD_LICENSES, error));
    }
  };
}

