export function loadAvailableServersThunk() {
  return async dispatch => {
    dispatch(creators.loading(LOAD_AVAILABLE_SERVERS));
    try {
      const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.AVAILABLE_SERVERS);
      const availableServersData = response.data.data;
      dispatch(creators.success(LOAD_AVAILABLE_SERVERS, availableServersData));
    } catch (error) {
      dispatch(creators.fail(LOAD_AVAILABLE_SERVERS, error));
    }
  };
}

export function updateLicenseAssignmentThunk(isInitialAssignment, payload) {
  return async () => {
    try {
      if (isInitialAssignment) {
        await RestClient.post(CONSTANTS.BASE.ENDPOINTS.SUBMIT_LICENSE_ASSIGNMENT, payload);
      } else {
        await RestClient.put(CONSTANTS.BASE.ENDPOINTS.UPDATE_LICENSE_ASSIGNMENT, payload);
      }
    } catch (error) {
      // eslint-disable-next-line no-console
      console.warn(error);
    }
  };
}
