import React from 'react';
import { LightTooltip } from '../../../shared/components';
import './ChartTooltip.local.scss';

export const ChartTooltip = (data) => {
  if (!data || !data.payload || data.payload.length < 3) {
    return '';
  }

  const assignedValue = data.payload[0].value;
  const usedValue = data.payload[1].value;
  const ownedValue = data.payload[2].value;
  return (
    <div className="exec-dashboard-chart-tooltip">
      <LightTooltip placement="bottom-end" title="Hello, World">
        <div className="exec-dashboard-chart-tooltip-content">
          <div className="exec-dashboard-chart-tooltip-row">
            <div className="exec-dashboard-chart-tooltip-name">Assigned</div>
            <div className="exec-dashboard-chart-tooltip-value">{assignedValue}</div>
          </div>
          <div className="exec-dashboard-chart-tooltip-row">
            <div className="exec-dashboard-chart-tooltip-name">Used</div>
            <div className="exec-dashboard-chart-tooltip-value">{usedValue}</div>
          </div>
          <div className="exec-dashboard-chart-tooltip-row">
            <div className="exec-dashboard-chart-tooltip-name">Owned</div>
            <div className="exec-dashboard-chart-tooltip-value">{ownedValue}</div>
          </div>
        </div>
      </LightTooltip>
    </div>
  );
};
